*{
  box-sizing: border-box;
}
body {
  background-color: rgb(51,56,63);
  font: 14px "Century Gothic", Futura, sans-serif;
}

ol, ul {
  padding-left: 30px;
}

.board-row {
  display: flex;
}

.status {
  margin-bottom: 10px;
  color: white;
  font-weight: bold;
  font-size: 1rem;
  text-align: center;
}

.square {
  background: transparent;
  border: none;
  font-size: 3rem;
  color: white;
  font-weight: bold;
  height: 4rem;
  padding: 0;
  text-align: center;
  width: 4rem;
  transition: all .5s ease;
}
.board-row:nth-child(3) .square:nth-child(2){
  border: 5px solid rgb(0,189,156);
}
.board-row:nth-child(2) .square:nth-child(1){
  border-style: solid;
  border-color: rgb(0,189,156);
  border-width: 0 5px 5px 0 ;
}
.board-row:nth-child(2) .square:nth-child(2){
  border-style: solid;
  border-color: rgb(0,189,156);
  border-width: 0 5px 5px 5px ;
}
.board-row:nth-child(2) .square:nth-child(3){
  border-style: solid;
  border-color: rgb(0,189,156);
  border-width: 0 0 5px 5px ;
}
.board-row:nth-child(3) .square:nth-child(1){
  border-style: solid;
  border-color: rgb(0,189,156);
  border-width: 5px 5px 5px 0 ;
}
.board-row:nth-child(3) .square:nth-child(3){
  border-style: solid;
  border-color: rgb(0,189,156);
  border-width: 5px 0 5px 5px ;
}
.board-row:nth-child(4) .square:nth-child(1){
  border-style: solid;
  border-color: rgb(0,189,156);
  border-width: 5px 5px 0 0 ;
}
.board-row:nth-child(4) .square:nth-child(2){
  border-style: solid;
  border-color: rgb(0,189,156);
  border-width: 5px 5px 0 5px ;
}
.board-row:nth-child(4) .square:nth-child(3){
  border-style: solid;
  border-color: rgb(0,189,156);
  border-width: 5px 0 0 5px ;
}

.square:focus {
  outline: none;
}
.reset{
  margin-top: 1rem;
  border: 1px solid white;
  background-color: transparent;
  color: white;
  font-weight: bold;
  padding: .5rem 1rem;
  border-radius: 10px;  
  transition: all .5s ease;
}
.reset:focus{
  color: rgb(0,189,156);
  border-color: rgb(0,189,156);
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  text-align: center;
  height: 100vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.game-info {
  margin-left: 20px;
}
